import config from './loan-b-approval-list.config.json'
import JsonViewer from '@/components/JsonViewer'
import moment from 'moment'
import VueNumeric from 'vue-numeric'
export default {
    name: 'loan-b-approval-list',
    data() {
        return {
            config,
            errorMessage: '',
            loan: {},
            loans: []
        }
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        actionApprove(_id, _code) {
            this.loan = { idLoan: _id, loanId: _code}
            this.$http({
                method: 'post',
                url: `/loan/approval/${this.loan.idLoan}/by-admin`,
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                },
                data: {
                    id_employee_admin: this.$store.getters.getUid,
                    decision: 'a'
                }
            })
            .then(response => {
                this.errorMessage = response.data.response
            })
            .catch(error => {
                this.errorMessage = error.response.data.response
            })
            .finally(() => {
                this.getLoans(this.employeeId)
            })
        },
        actionReject(_id, _code) {
            this.loan = { idLoan: _id, loanId: _code}
            this.$http({
                method: 'post',
                url: `/loan/approval/${this.loan.idLoan}/by-admin`,
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                },
                data: {
                    id_employee_admin: this.$store.getters.getUid,
                    decision: 'r'
                }
            })
            .then(response => {
                console.log(response.data)
            })
            .catch(error => {
                console.log(error.response.data)
                this.errorMessage = error.response.data.response
            })
            .finally(() => {
                this.getLoans(this.employeeId)
            })
        },
        formatDate(_date) {
            return _date !== '' ? moment(_date).format('DD-MMM-YYYY') : '-'
        },
        getLoans() {
            this.$http({
                method: 'get',
                url: '/loan/view/list-of-loan-b',
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                this.loans = response.data
            })
            .catch(error => {
                this.errorMessage = error.response.data.message
            })
        },
        getStatus(_status) {
            let status = ''
            switch (_status) {
                case 's':
                    status = 'SUBMITTED'
                    break
                case 'a':
                    status = 'APPROVED'
                    break
                case 'r':
                    status = 'REJECTED'
                    break
                case 'd':
                    status = 'DISBURSED'
                    break
                default:
                    status = ''
            }
            return status
        },
        resetErrorMessage() {
            this.errorMessage = ''
        },
        showModalApprovalConfirmation(_id, _code) {
            this.loan = { idLoan: _id, loanId: _code}
            this.$bvModal.show('modal-approval-confirmation')
        },
        showModalRejectionConfirmation(_id, _code) {
            this.loan = { idLoan: _id, loanId: _code}
            this.$bvModal.show('modal-rejection-confirmation')
        }
    },
    created() {
        this.getLoans()
    }
}